.mainPage {
  max-width: 1440px;
  margin: 0 auto;
  .mission {
    width: 100%;
    background-repeat: no-repeat;
    padding: 2% 0 14%;
    background: #041016;
    border-top-left-radius: 72px;
    border-top-right-radius: 72px;
    h1 {
      padding-left: 14%;
      font-family: Dela Gothic One;
      font-style: normal;
      font-weight: normal;
      font-size: 62px;
      line-height: 110px;
      background: -webkit-linear-gradient(-2deg, #327ba9 0%, #07161f 90%);
      background: -moz-linear-gradient(-2deg, #327ba9 0%, #07161f 90%);
      background: linear-gradient(-2deg, #327ba9 0%, #07161f 90%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    .missions {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      width: 80%;
      padding-inline: 10%;
      @media (max-width: 800px) {
        width: 90%;
        padding-inline: 5%;
      }
      @media (max-width: 500px) {
        align-items: center;
        flex-direction: column;
      }
      div {
        display: flex;
        flex-direction: column;
        align-items: left;
        width: 33%;
        @media (max-width: 500px) {
          width: 60%;
        }
        .light {
          position: absolute;
          width: 300px;
          height: 232px;
          opacity: 0.2;
          filter: blur(200px);
          z-index: 0;
          @media (max-width: 1024px) {
            width: 200px;
            height: 202px;
          }
          @media (max-width: 500px) {
            width: 160px;
            height: 162px;
          }
        }
        img {
          align-self: center;
          width: 80%;
          z-index: 9999;
          @media (max-width: 500px) {
            width: 100%;
          }
        }
        h2 {
          font-family: Montserrat;
          font-size: 28px;
          margin: -4% 0 0;
          padding-left: 10%;
          width: 80%;
          z-index: 9999;

          @media (max-width: 1200px) {
            font-size: 22px;
            width: 86%;
          }
          @media (max-width: 800px) {
            font-size: 18px;
          }
          @media (max-width: 500px) {
            width: 100%;
            padding-left: 0%;
          }
        }
        p {
          font-family: Montserrat;
          font-weight: 500;
          padding-left: 10%;
          width: 70%;
          font-size: 16px;
          line-height: 24px;
          z-index: 9999;

          @media (max-width: 1200px) {
            font-size: 12px;
            line-height: 18px;
          }
          @media (max-width: 800px) {
            font-size: 12px;
            line-height: 15px;
            width: 84%;
          }
          @media (max-width: 500px) {
            font-size: 12px;
            line-height: 20px;
            padding-left: 0%;
            width: 100%;
          }
        }
      }
    }
  }

  // CHARACTERS
  .characters {
    width: 85.2%;
    margin: 0 auto;
    background: #06141d;
    border-radius: 72px;
    margin-top: -5%;
    padding: 3% 7.4% 0;
    // @media (max-width: 800px) {
    //   width: 90%;
    //   padding-inline: 5%;
    // }
    h1 {
      font-family: Dela Gothic One;
      font-style: normal;
      font-weight: normal;
      font-size: 50px;
      line-height: 58px;
      background: -webkit-linear-gradient(
        45deg,
        #ffffff 0%,
        #ffcd95 20%,
        #764204 80%
      );
      background: -moz-linear-gradient(
        45deg,
        #ffffff 0%,
        #ffcd95 20%,
        #764204 80%
      );
      background: linear-gradient(45deg, #ffffff 0%, #ffcd95 20%, #764204 80%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-left: 5%;
      margin-bottom: 0;
      @media (max-width: 1200px) {
        font-size: 52px;
        line-height: 52px;
        margin-bottom: 4%;
      }
      @media (max-width: 800px) {
        font-size: 30px;
        line-height: 30px;
        margin-left: 10%;
      }
    }
    .description {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-left: 5%;
      p {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 26px;
        color: #63a3c9;
        width: 58%;
      }
      .icons {
        display: flex;
        margin: 2% 0;
        width: 12%;
        @media (max-width: 800px) {
          margin-left: 10%;
        }
        div {
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 10%;
          background: #0a1e29;
          border-radius: 50%;
          img {
            width: 100%;
          }
        }
      }
      @media (max-width: 1200px) {
        font-size: 16px;
        line-height: 18px;
      }
      @media (max-width: 800px) {
        font-size: 12px;
        line-height: 14px;
        margin-left: 10%;
        width: 80%;
      }
    }

    .slider {
      .arrow {
        background: #101e2b;
        border-radius: 20px;
        height: 100%;
        padding-inline: 1.6%;
        width: 3%;
        z-index: 9999;
        @media (max-width: 1024px) {
          display: none !important;
        }
      }
      .botsFrame {
        display: flex !important;
        align-items: center;
        background: rgba(64, 91, 109, 0.1);
        width: 88% !important;
        max-height: 400px;
        border-radius: 32px;
        margin: 0 auto;
        @media (max-width: 1024px) {
          min-height: 45vw;
          flex-direction: column;
          justify-content: center;
        }
        @media (max-width: 800px) {
          width: 94% !important;
          min-height: 58vw;
        }
        @media (max-width: 600px) {
          min-height: 58vw;
        }
        @media (max-width: 500px) {
          min-height: 72vw;
        }
        @media (max-width: 500px) {
          min-height: 78vw;
        }
        &:nth-child(1) {
          .botInfo {
            h2 {
              width: 120% !important;
            }
          }
        }
        img {
          width: 50vw;
          max-width: 780px;
          margin: -9% -11% -9% -8%;
          @media (max-width: 1024px) {
            margin: -10% -3% -6% -8%;
            width: 40vw;
          }
          @media (max-width: 800px) {
            margin: -14% -3% -12% -8%;
            width: 53vw;
          }
          @media (max-width: 500px) {
            margin: -16% -3% -12% -8%;
            width: 58vw;
          }
        }
        .botInfo {
          padding-right: 4%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: 20em;
          @media (max-width: 1024px) {
            height: 15em;
            width: 70%;
          }
          @media (max-width: 800px) {
            height: 12em;
          }
          @media (max-width: 800px) {
            width: 90%;
          }
          h2 {
            text-transform: uppercase;
            font-family: Dela Gothic One;
            margin-bottom: 0%;
            width: 100%;
            @media (max-width: 1200px) {
              font-size: 26px;
              line-height: 26px;
            }
            @media (max-width: 800px) {
              font-size: 20px;
              line-height: 20px;
            }
            @media (max-width: 500px) {
              font-size: 15px;
              line-height: 15px;
            }
          }
          p {
            margin-top: 2%;
            font-family: "Montserrat";
            @media (max-width: 1200px) {
              font-size: 14px;
              line-height: 16px;
            }
            @media (max-width: 600px) {
              font-size: 12px;
              line-height: 13px;
            }
          }
        }
        footer {
          display: flex;
          width: auto;
          div {
            display: flex;
            flex-direction: column;
            margin-right: 4%;
            width: 80px;
            @media (max-width: 800px) {
              width: 60px;
            }
            span {
              display: flex;
              justify-content: center;
              border-radius: 12px;
              padding: 6% 0%;
              @media (max-width: 800px) {
                font-size: 12px;
                padding: 6% 0%;
              }
              &:last-child {
                margin: 2% 0;
              }
            }
          }
        }
        h2 {
          font-family: Dela Gothic One;
          font-style: normal;
          font-weight: normal;
          font-size: 36px;
          line-height: 40px;
          @media (max-width: 1200px) {
            font-size: 26px;
            line-height: 26px;
          }
          @media (max-width: 1024px) {
            width: 70%;
          }
          @media (max-width: 800px) {
            font-size: 22px;
            line-height: 22px;
          }
          @media (max-width: 600px) {
            font-size: 16px;
            line-height: 16px;
          }
        }
      }
    }
  }

  .howToPlay {
    width: 88%;
    margin: 0 auto;
    @media (max-width: 800px) {
      margin-top: 6%;
    }
    h1 {
      position: relative;
      font-family: Dela Gothic One;
      font-style: normal;
      font-weight: normal;
      font-size: 62px;
      line-height: 110px;
      background: -webkit-linear-gradient(-2deg, #327ba9 0%, #07161f 90%);
      background: -moz-linear-gradient(-2deg, #327ba9 0%, #07161f 90%);
      background: linear-gradient(-2deg, #327ba9 0%, #07161f 90%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-left: 8%;
      z-index: 9999;
      @media (max-width: 1200px) {
        font-size: 52px;
        line-height: 52px;
        margin-bottom: 4%;
      }
      @media (max-width: 800px) {
        font-size: 30px;
        margin-left: 10%;
        line-height: 30px;
      }
    }
    .howToPlayContent {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @media (max-width: 1124px) {
        width: 70%;
        margin: 0 auto;
      }
      @media (max-width: 800px) {
        width: 90%;
      }
      @media (max-width: 600px) {
        justify-content: center;
      }
      .gameSteps {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 23%;
        min-width: 240px;
        background: linear-gradient(
          180deg,
          #111f28 0%,
          rgba(17, 31, 40, 0) 100%
        );
        z-index: 9999;
        border-radius: 40px;
        @media (max-width: 1200px) {
          margin-top: 4%;
        }
        @media (max-width: 800px) {
          margin-top: 6%;
        }
        @media (max-width: 600px) {
          margin-top: 8%;
        }
        &:nth-child(1) {
          .border {
            height: 23em;
          }
          main {
            margin-top: 1.6em;
            @media (max-width: 1412px) {
              margin-top: -1.6em;
            }
            .stepInfo {
              margin-top: 3.2em;
              @media (max-width: 1200px) {
                margin-top: 3.3em;
              }
              &:last-child {
                margin-top: 2.3em;
                @media (max-width: 1200px) {
                  margin-top: 3em;
                }
              }
              p {
                max-height: 2em;
              }
            }
          }
        }
        &:nth-child(2) {
          .border {
            height: 24.7em;
          }
          main {
            margin-top: 5.4em;
            @media (max-width: 1605px) {
              margin-top: 4.2em;
            }
            @media (max-width: 1426px) {
              margin-top: 0em;
            }
            .stepInfo {
              margin-top: 2.6em;
              &:last-child {
                margin-top: 3.2em;
                @media (max-width: 1200px) {
                  margin-top: 4.1em;
                }
              }
              p {
                max-height: 3em;
              }
            }
          }
        }
        &:nth-child(3) {
          .border {
            height: 34.7em;
          }
          main {
            margin-top: 0m;
            .stepInfo {
              margin-top: 3.3em;
              @media (max-width: 1200px) {
                margin-top: 3.6em;
              }
              &:last-child {
                margin-top: 5em;
                @media (max-width: 1200px) {
                  margin-top: 4.8em;
                }
              }
              p {
                max-height: 2em;
              }
            }
          }
        }
        &:nth-child(4) {
          .border {
            height: 9.7em;
          }
        }
        .mainPic {
          width: 90%;
          margin-top: -6%;
          margin-bottom: 5%;
          z-index: 9999;
          @media (max-width: 1024px) {
            width: 70%;
          }
        }
        .light {
          position: absolute;
          width: 160px;
          height: 160px;
          opacity: 0.2;
          margin-top: 1%;
          filter: blur(60px);
          z-index: 0;
          @media (max-width: 800px) {
            width: 120px;
            height: 120px;
            filter: blur(60px);
          }
        }
        .gameStepsInfo {
          display: flex;
          align-items: flex-start;
          z-index: 9999;
          .gameInfo {
            margin-left: 5%;
            h3 {
              margin-top: -4%;
              font-family: Dela Gothic One;
              font-style: normal;
              font-weight: normal;
              font-size: 20px;
              line-height: 28px;
              text-transform: uppercase;
              @media (max-width: 1200px) {
                font-size: 16px;
                line-height: 24px;
              }
            }
            .description {
              margin: 0;
              width: 80%;
              @media (max-width: 1200px) {
                font-size: 14px;
                line-height: 20px;
              }
            }
            main {
              .stepInfo {
                div {
                  display: flex;
                  align-items: center;
                  h4 {
                    margin: 0 0 0 6%;
                  }
                }
                p {
                  max-height: 1.8em;
                  width: 98%;
                  margin: 4% 2% 0 0;
                  @media (max-width: 1200px) {
                    font-size: 14px;
                    line-height: 20px;
                  }
                }
              }
              .adittionalIcons {
                margin-top: 3.2em;
                @media (max-width: 1200px) {
                  margin-top: 3.5em;
                }
                .icons {
                  display: flex;
                  margin: 2% 0;
                  div {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: 2%;
                    padding: 5%;
                    @media (max-width: 1200px) {
                      padding: 6%;
                    }
                    background: #0a1e29;
                    border-radius: 50%;
                    img {
                      width: 80%;
                    }
                  }
                }
                h4 {
                  font-family: Montserrat;
                  font-style: normal;
                  font-weight: bold;
                  font-size: 18px;
                  line-height: 24px;
                }
              }
            }
          }
        }
      }
    }
  }

  // DISTRIBUTION
  .distribution {
    width: 100%;
    margin: 0 auto;
    max-width: 1440px;
    overflow: visible;
    @media (max-width: 800px) {
      margin-top: 8%;
    }
    h1 {
      font-family: Dela Gothic One;
      font-style: normal;
      font-weight: normal;
      font-size: 50px;
      line-height: 58px;
      background: -webkit-linear-gradient(
        45deg,
        #ffffff 0%,
        #ffcd95 20%,
        #764204 80%
      );
      background: -moz-linear-gradient(
        45deg,
        #ffffff 0%,
        #ffcd95 20%,
        #764204 80%
      );
      background: linear-gradient(45deg, #ffffff 0%, #ffcd95 20%, #764204 80%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      width: 40%;
      margin-left: 5%;
      z-index: 9999;
      position: relative;
      @media (max-width: 1200px) {
        font-size: 52px;
        line-height: 52px;
        margin-bottom: 4%;
      }
      @media (max-width: 800px) {
        font-size: 26px;
        line-height: 26px;
        width: 90%;
      }
    }
    .canvas {
      display: flex;
      flex-wrap: wrap;
      width: 110em;
      margin-left: -8%;
      margin-bottom: 6%;
    }

    .comb {
      width: 170px;
      height: 114px;
      position: relative;
      display: inline-block;
      margin-bottom: 185px;
    }

    .comb:nth-of-type(2n) {
      top: 150px;
    }
  }
  .roadmap {
    width: 100%;
    .roadTitle {
      font-family: Dela Gothic One;
      font-style: normal;
      font-weight: normal;
      font-size: 50px;
      line-height: 110px;
      margin-left: 5%;
    }
    .left,
    .right {
      position: absolute;
      z-index: -9999;
      margin-top: 0;
      background-size: 100% auto;
      background-repeat: no-repeat;
      width: 12em;
      height: 100%;
    }
    .left {
      margin-left: -0.4%;
    }
    .right {
      margin-top: -1%;
      margin-left: 74.8em;
    }
    .years {
      display: flex;
      justify-content: space-between;
      width: 50%;
      align-self: flex-start;
      margin-left: 5%;
      z-index: 9999;
      &:after {
        position: absolute;
        z-index: -9999;
        margin-top: 2.5em;
        content: "";
        height: 2px;
        width: 90%;
        max-width: 1280px;
        background: #182e3b;
      }
      div {
        font-family: Dela Gothic One;
        font-style: normal;
        font-weight: normal;
        font-size: 26px;
        line-height: 37px;
        text-transform: uppercase;
        color: #182e3c;
        &:first-child {
          .active,
          :hover {
            color: #4cff9e;
            border-bottom: 6px solid #4cff9e;
            z-index: 9999;
          }
        }
        &:nth-child(2) {
          .active,
          :hover {
            color: #ff59b4;
            border-bottom: 6px solid #ff59b4;
          }
        }
        &:nth-child(3) {
          .active,
          :hover {
            color: #7fe8ff;
            border-bottom: 6px solid #7fe8ff;
          }
        }
        &:last-child {
          color: #182e3c;
          .active,
          :hover {
            color: #244458;
            border-bottom: 6px solid #244458;
          }
        }
      }
    }
    .slider {
      width: 90%;
      margin: 0 auto;
      .q_container {
        display: flex !important;
        align-items: flex-start;
        margin-top: 4%;
        .q_block {
          display: flex;
          flex-direction: column;
          justify-content: center;
          background: #0b1a23;
          border-radius: 20px;
          margin-right: 2%;
          padding: 1% 2%;
          min-width: 19%;
          &:last-child {
            margin-right: 1%;
          }
          h1 {
            font-family: Dela Gothic One;
            font-style: normal;
            font-weight: normal;
            font-size: 33px;
            line-height: 37px;
            text-transform: uppercase;
            color: #4cff9e;
            margin-top: 0;
          }
          .curveLine {
            background: #07161f;
            backdrop-filter: blur(20px);
            border-radius: 20px;
            transform: rotate(-45deg);
            margin: 33% -8% 50%;
            width: 254px;
            height: 4px;
          }
          p {
            font-family: Montserrat;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            border-bottom: 2px solid #07161f;
            margin-top: 0;
            padding-bottom: 2%;
            &:last-child {
              padding-bottom: 0%;
              border-bottom: none;
            }
          }
        }
      }
      .slick-dots {
        display: none !important;
      }
    }
  }
  // TEAM
  .team {
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    margin-left: 4%;
    margin-top: -4%;
    @media (max-width: 800px) {
      margin-top: 6%;
    }
    .blockInfo {
      min-width: 22%;
      z-index: 9999;
      @media (max-width: 800px) {
        min-width: 30%;
      }
      @media (max-width: 600px) {
        min-width: 40%;
      }
      @media (max-width: 500px) {
        min-width: 45%;
      }
      h1 {
        font-family: "Dela Gothic One", cursive;
        background: -webkit-linear-gradient(
          45deg,
          #ffffff 0%,
          #ffcd95 40%,
          #764204 100%
        );
        background: -moz-linear-gradient(
          45deg,
          #ffffff 0%,
          #ffcd95 40%,
          #764204 100%
        );
        background: linear-gradient(
          45deg,
          #ffffff 0%,
          #ffcd95 40%,
          #764204 100%
        );
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-family: Dela Gothic One;
        font-style: normal;
        font-weight: normal;
        font-size: 82px;
        line-height: 110px;
        margin-bottom: 0%;
        @media (max-width: 1200px) {
          font-size: 52px;
          line-height: 52px;
          margin-bottom: 4%;
        }
        @media (max-width: 800px) {
          font-size: 30px;
          line-height: 30px;
        }
      }
      p {
        margin-top: 2%;
        font-family: "Montserrat", sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 28px;
        @media (max-width: 1200px) {
          margin-top: 0%;
          font-size: 14px;
          line-height: 22px;
        }
        @media (max-width: 800px) {
          font-size: 12px;
          line-height: 18px;
        }
      }
    }
    .members {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      margin-top: 6%;
      margin-left: -14%;
      margin-bottom: -4%;
      background-repeat: no-repeat;
      // @media (max-width: 800px) {
      //   margin-left: -26%;
      //   min-width: 135em;
      // }
      // @media (max-width: 500px) {
      //   min-width: 115em;
      // }
      // @media (max-width: 600px) {
      //   margin-left: -36%;
      // }
      // @media (max-width: 500px) {
      //   margin-left: -54%;
      // }
      .memberInfo {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 0%;
        width: 24%;
        margin-left: -2%;
        // @media (max-width: 800px) {
        //   // margin-left: -2.8%;
        // }
        // @media (max-width: 500px) {
        //   margin-inline: -2%;
        // }
        .memberPic {
          width: 100%;
          height: auto;
          @media (max-width: 1200px) {
            width: 70%;
          }
          @media (max-width: 800px) {
            width: 60%;
          }
          @media (max-width: 600px) {
            width: 50%;
          }
        }
        .memberText {
          position: absolute;
          display: flex;
          flex-direction: column;
          width: 11%;
          margin-top: 16%;
          margin-left: 1%;
          @media (max-width: 1200px) {
            margin-top: 8%;
            width: 5%;
          }

          h4 {
            margin: 0;
            font-family: Dela Gothic One;
            font-style: normal;
            font-weight: normal;
            font-size: 19px;
            line-height: 22px;
            @media (max-width: 1200px) {
              font-size: 14px;
              line-height: 18px;
            }
            @media (max-width: 800px) {
              font-size: 12px;
              line-height: 14px;
            }
          }
          span {
            font-family: "Dela Gothic One", cursive;
            font-style: normal;
            font-weight: normal;
            font-size: 13px;
            line-height: 16px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #c9a061;
            margin: 3% 0;
            @media (max-width: 1200px) {
              font-size: 11px;
              line-height: 13px;
            }
            @media (max-width: 800px) {
              font-size: 9px;
              line-height: 13px;
            }
          }
          img {
            @media (max-width: 800px) {
              width: 20%;
            }
          }
        }
        &:first-child {
          margin-left: 9%;
        }

        &:nth-child(n + 5) {
          margin-top: -21%;
          margin-left: -2%;
          margin-right: 0%;
          @media (max-width: 1200px) {
            margin-top: -14%;
          }
          @media (max-width: 800px) {
            // margin-left: -2.8%;
          }
          @media (max-width: 600px) {
            margin-top: -10%;
          }
        }
        &:nth-child(5) {
          margin-left: -3%;
        }
        &:nth-child(9) {
          margin-left: 3%;
        }
        // &:nth-child(n + 10) {
        //   margin-left: 2%;
        //   @media (max-width: 500px) {
        //     margin-left: -2%;
        //   }
        // }
        // &:nth-child(n + 9) {
        //   margin-left: 4%;
        //   @media (max-width: 500px) {
        //     margin-left: 0%;
        //   }
        // }
        // &:nth-child(n + 11) {
        //   margin-left: 3%;
        //   @media (max-width: 500px) {
        //     margin-left: -1%;
        //   }
        // }
      }
    }
  }
