header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding-bottom: 2%;
  // @media (max-width: 800px) {
  //   width: 64%;
  //   padding-inline: 12%;
  // }
  // @media (max-width: 600px) {
  //   width: 90%;
  //   padding-inline: 5%;
  // }
  .topBarContainer {
    display: flex;
    background-color: #0b1e29;
    height: 68px;
    width: 100%;
    .topBar {
      display: flex;
      justify-content: space-between;
      padding-inline: 6%;
      max-width: 1440px;
      margin: 0 auto;
      width: 100%;
      a {
        padding-top: 1.5%;
        width: 10%;
        img {
          width: 100%;
        }
      }
      .barCenter {
        display: flex;
        align-self: center;
        justify-content: space-between;
        width: 20%;
      }
      .additionalMenu {
        display: flex;
        align-self: center;
        justify-content: space-between;
        width: 17%;
        span {
          padding: 4% 8%;
          border-radius: 11px;
          &:first-child {
            background-color: #112d3d;
          }
          &:last-child {
            background: linear-gradient(94.04deg, #e8b86f 1.76%, #523112 94.7%);
          }
        }
      }
    }
  }
  .contentContainer {
    .content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 80%;
      padding-inline: 10%;
      margin: 0 auto;
      margin-top: 5%;
      padding-top: 5%;
      background-size: 80% 100%;
      background-position: center;
      background-repeat: no-repeat;
      max-width: 1440px;
      .info {
        width: 60%;
        h1 {
          font-family: Dela Gothic One;
          font-style: normal;
          font-weight: normal;
          font-size: 64px;
          line-height: 66px;
          text-transform: uppercase;
          margin: 0% 0%;
        }
        p {
          font-family: Montserrat;
          font-style: normal;
          font-weight: 500;
          font-size: 18px;
          line-height: 28px;
        }
      }
      .threeD {
        width: 40%;
        .links {
          display: flex;
          justify-content: space-around;
          a {
            border-radius: 14px;
            color: white;
            text-decoration: none;
            padding: 2% 10%;
            font-family: Dela Gothic One;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 18px;
            &:first-child {
              background: rgba(0, 255, 163, 0.1);
              border: 2px solid #00ffa3;
            }
            &:last-child {
              background: rgba(114, 28, 255, 0.1);
              border: 2px solid #721cff;
            }
          }
        }
      }
    }
  }

  footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-self: flex-start;
    width: 22%;
    margin-left: 10%;
    margin-top: 5.2%;
    margin-bottom: -3.6%;
    div {
      display: flex;
      justify-content: center;
      background: #122632;
      border-radius: 16px;
      padding: 4%;
      img {
        width: 22px;
        height: 20px;
      }
    }
  }
